// slider start

const myslide = document.querySelectorAll(".myslider");
dot = document.querySelectorAll(".dot");

let slideCounter = 1;
slidefun();

let timer = setInterval(autoslide, 3000);

function autoslide() {
  slideCounter += 1;
  slidefun();
}

function prevSlide() {
  slideCounter--;
  if (slideCounter === 0) slideCounter = 4;
  slidefun();
  resetTimer();
}

function nextSlide() {
  slideCounter++;
  if (slideCounter === 5) slideCounter = 1;
  slidefun();
  resetTimer();
}

function currentSlide(n) {
  slideCounter = n;
  slidefun();
  resetTimer();
}
function resetTimer() {
  clearInterval(timer);
  timer = setInterval(autoslide, 3000);
}

function slidefun() {
  let i;
  for (i = 0; i < myslide.length; i++) {
    myslide[i].style.display = "none";
  }
  for (i = 0; i < dot.length; i++) {
    dot[i].classList.remove("active");
  }
  if (slideCounter > myslide.length) {
    slideCounter = 1;
  }
  if (slideCounter < 1) {
    slideCounter = myslide.length;
  }
  myslide[slideCounter - 1].style.display = "block";
  dot[slideCounter - 1].classList.add("active");
}

// slider end

// counter section start

const counters = document.querySelectorAll(".counter");
const speed = 1600;

counters.forEach((counter) => {
  const updateCount = () => {
    const target = +counter.getAttribute("data-target");
    const count = +counter.innerText;

    const inc = target / speed;

    if (count < target) {
      counter.innerText = Math.ceil(count + inc);
      setTimeout(updateCount, 1);
    } else {
      count.innerText = target;
    }
  };
  // start counter when the user reaches a certain height
  const startCount = (position, counting) => {
    window.addEventListener("scroll", function (e) {
      scroll_position = window.scrollY;
      if (scroll_position > position) {
        counting();
      }
    });
  };

  startCount(3929, updateCount);
  //
});

// counter section end

//Sticky Header
window.onscroll = function () {
  stickyHeader();
};

var header = document.querySelector(".bottomNav");

var sticky = header.offsetTop;

function stickyHeader() {
  if (window.pageYOffset > sticky) {
    header.classList.add("sticky");
  } else {
    header.classList.remove("sticky");
  }
}
